body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.AuthorsList_ListDiv__1PGzn {
    width: calc(100% - 20px);
    height: 40px;
    padding: 10px;
    display: table;
}

.AuthorsList_TopDiv__15DCL {
    text-align: left;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.AuthorsList_HeaderDiv__3zcs5 {
    height: 30px;
}

.AuthorsList_BottomDiv__3zBrN {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.AuthorsList_WhiteDiv__3ohR4 {
    background-color: white;
}

.AuthorsList_GrayDiv__586bZ {
    background-color: rgb(233, 238, 243);
}

.AuthorsList_RowDiv__1NFRz {
    display: table-row;
}

.AuthorsList_ContentDiv__16TL1 {
    display: table-cell;
    vertical-align: middle;
}

.AuthorsList_LastDiv__1ehP8 {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.AuthorsList_NavigationDiv__2X4rH {
    height: 40px;
    text-align: center;
}

.AuthorsList_Icon__1isZY {
    vertical-align: middle; 
}

.AuthorsList_MedalLogo__1FOjx {
    height: 40px;
}

.AuthorsList_Input__1rw0V {
    height: 20px;
    width: 400px;
    padding: 0 5px;
    margin: 0 5px;
    background-color: rgb(233, 238, 243);
    border: none;
    font-weight: bold;
}

.AuthorsList_NumDiv__1H7gP {
    height: 100%;
    width: 30px;
    padding-right: 10px;
    color: gray;
    display: table-cell;
    vertical-align: middle;
}

.AuthorsList_NameCircle__1ejoe {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    color: white;
    background-color: red;
    font-weight: bold;
    display: table-cell;
    vertical-align: middle;
    box-shadow: 0 0 10px 10px rgb(193, 198, 204);
}

i {
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
  }

.AuthorsList_RightArrow__1moVn {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.AuthorsList_LeftArrow__cRwxK {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.AuthorsList_UpArrow__2Toea {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.AuthorsList_DownArrow__eXiVE {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.App {
  min-height: calc(100vh - 30px);
  text-align: center;
  background-image: -webkit-linear-gradient(rgb(18, 43, 63), rgb(40, 95, 137));
  background-image: linear-gradient(rgb(18, 43, 63), rgb(40, 95, 137));
  padding: 15px;
  overflow: auto;
}
